<template>


  <div id="meeting-user" >
    <el-form :model="CreateMeeting" :rules="rules" :ref="meetingForm" class="meeting" v-loading="loading">

      <h2 class="CreateMeeting_title">Create Meeting</h2>

      <el-form-item  class="single"  type="flex" flex-wrap="nowrap">
          <el-form-item class="words"><el-div>Conference abbreviation:</el-div></el-form-item>

          <el-form-item class="inputStyle" prop="AbbreviationName">
            <el-input
              type="text"
              auto-complete="off"
              required="required"
              v-model="CreateMeeting.AbbreviationName"
              placeholder="Conference abbreviation">Conference abbreviation:
            </el-input>
          </el-form-item>
      </el-form-item>

      <el-form-item  class="single"  type="flex" flex-wrap="nowrap">
        <el-form-item class="words"><el-div>Full name of the conference:</el-div></el-form-item>

        <el-form-item class="inputStyle" prop="FullName">
          <el-input
            type="text"
            required="required"
            auto-complete="off"
            v-model="CreateMeeting.FullName"
            placeholder="Full name of the conference">
          </el-input>
        </el-form-item>
      </el-form-item>

      <el-form-item  class="single"  type="flex" flex-wrap="nowrap">
        <el-form-item class="words"><el-div>Venue:</el-div></el-form-item>

        <el-form-item class="inputStyle" prop="place">
          <el-input
            type="text"
            required="required"
            v-model="CreateMeeting.place"
            auto-complete="off"
            placeholder="Venue">
          </el-input>
        </el-form-item>
      </el-form-item>

      <el-form-item  class="single"  type="flex" flex-wrap="nowrap">
        <el-form-item class="words"><el-div>Holding time:</el-div></el-form-item>

        <el-form-item class="inputStyle" prop="HoldingTime">
          <el-input
            type="date"
            auto-complete="off"
            required="required"
            v-model="CreateMeeting.HoldingTime"
            placeholder="Holding time">
          </el-input>
        </el-form-item>
      </el-form-item>

      <el-form-item  class="single"  type="flex" flex-wrap="nowrap">
        <el-form-item class="words"><el-div>Deadline for submission:</el-div></el-form-item>

        <el-form-item class="inputStyle" prop="DDLforSubmission">
          <el-input
            type="date"
            auto-complete="off"
            required="required"
            v-model="CreateMeeting.DDLforSubmission"
            placeholder="Deadline for submission">
          </el-input>
        </el-form-item>
      </el-form-item>


      <el-form-item  class="single"  type="flex" flex-wrap="nowrap">
        <el-form-item class="words"><el-div>Release date of review results:</el-div></el-form-item>

        <el-form-item class="inputStyle" prop="ReleaseDate">
          <el-input
            type="date"
            auto-complete="off"
            required="required"
            v-model="CreateMeeting.ReleaseDate"
            placeholder="Deadline for submission">
          </el-input>
        </el-form-item>
      </el-form-item>

      <el-form-item class="check-button">

        <el-button type="primary" v-on:click="submitFrom()" style="width: 150px;height:25px;background: #afb4db;border: none;position: relative">Create</el-button>

      </el-form-item>

    </el-form>


  </div>

</template>





<script>


    export default {

        name: "User",
      data(){
          //判断投稿日期是否比举办日期早
        var DDLofSubmissionCheck= (rule, value, callback) =>{

          if(!value ) {
            return callback(new Error('不能为空'))
          }
          if(value>=this.CreateMeeting.HoldingTime){
            callback(new Error("投稿截止日期应早于举办时间"))
          }

          return callback()
        };
        //判断评审日期比举办日期早，截稿日期迟
        var ReleaseDateCheck= (rule, value, callback) =>{

          if(!value ) {
            return callback(new Error('不能为空'))
          }
          if (value>=this.CreateMeeting.HoldingTime||value<=this.CreateMeeting.DDLforSubmission){
            callback(new Error("评审结果发布日期应早于举办时间，迟于投稿截止日期"))
          }
          return callback()
        };

        const dataValid = (rule, value, callback) => {
          if(!value ) {
            return callback(new Error('不能为空'))
          }
          return callback()
        }
          return{
            CreateMeeting:{
              AbbreviationName:'',
              FullName:'',
              place:'',
              HoldingTime:'',
              DDLforSubmission:'',
              ReleaseDate:'',
            },

            rules:{
              AbbreviationName: [{required: true, message: '会议简称不能为空', trigger: 'blur'},
                {validator:dataValid,trigger:"blur"}],
              FullName: [{required: true, message: '会议全称不能为空', trigger: 'blur'},
                {validator:dataValid,trigger:"blur"}],
              place: [{required: true, message: '举办地点不能为空', trigger: 'blur'},
                {validator:dataValid,trigger:"blur"}],
              HoldingTime: [{required: true, message: '举办时间不能为空', trigger: 'blur'},
                {validator:dataValid,trigger:"blur"}],
              DDLforSubmission: [{required: true, message: '投稿截止日期不能为空', trigger: 'blur'},
                {validator:DDLofSubmissionCheck,trigger:"blur"}],
              ReleaseDate: [{required: true, message: '评审结果发布日期不能为空', trigger: 'blur'},
                {validator:ReleaseDateCheck,trigger:"blur"}],
            },

          }
      },

      methods: {

        submitFrom(){
          alert("申请成功")
        }
/*
        submitFrom(formName){



          this.$refs[formName].validator(valid=> {

            if (valid){
              this.$axios
                .post("/component/user",{
                  AbbreviationName:this.CreateMeeting.AbbreviationName,
                  FullName:this.CreateMeeting.FullName,
                  place:this.CreateMeeting.place,
                  HoldingTime:this.CreateMeeting.HoldingTime,
                  DDLforSubmission:this.CreateMeeting.DDLforSubmission,
                  ReleaseDate:this.CreateMeeting.ReleaseDate
                })

                .then(resp=>{
                  this.$message({
                    message: "注册成功！",
                    type: "success"
                  });
                });
            }else {
              console.log("error submit!!");
              return false;

            }




          });




        }



 */

      }


    }
</script>

<style scoped>
  #meeting-user{
    background: url("../assets/background/background.jpg") repeat;
    background-position: center;
    height: 100%;
    width: 100%;
    background-size: cover;
    position: fixed;
  }
  .meeting{
    width: 600px;
    height: 430px;
    background-color: aliceblue;
    margin: auto;
    margin-top: 100px;
    border-bottom-left-radius: 10px;
    border-top-right-radius:10px;
    border-top-left-radius:10px;
    border-bottom-right-radius:10px;
  }
  .CreateMeeting_title{
    margin: auto;
    height: 70px;
    padding-top: 30px;
  }
  .single{
    width: 600px;
    height: 40px;
  }
  .inputStyle{
    width: 240px;
    height: 35px;
    display: inline-block;
  }

  .words{
    width: 300px;
    height: 35px;
    display: inline-block;
  }
  .check-button{
    margin: auto;
    padding-top: 30px;
    height: 50px;

  }

</style>



