/* 验证账号 */
export function validateUsername(rule, value, callback) {
  const usernameReg = /^[a-zA-Z-][a-zA-Z0-9-_]{4,31}$/
  if (!value) {
    return callback(new Error('不能为空'))
  }
  setTimeout(() => {
    if (!usernameReg.test(value)) {
      return callback(new Error('用户名只能包含字母、数字或两种特殊字符（-_）且只能以字母或-开头，长度为5-32个字符'))
    }
    else {
      callback()
    }}, 100)
}

/* 验证密码 */
export function validatePassword(rule, value, callback) {
  const passwordReg = /^(?![\d]+$)(?![a-zA-Z]+$)(?![-_]+$)[\da-zA-Z-_]{6,32}$/
  if (!value) {
    return callback(new Error('不能为空'))
  }
  setTimeout(() => {
    if (!passwordReg.test(value)) {
      return callback(new Error('密码中字母、数字或者特殊字符（-_）至少包含两种，长度为6-32个字符'))
    }
    else if(value.contains(document.getElementById("usn").value)){
      return callback(new Error('密码中不能包含用户名'))
    }
    else {
      callback()
    }}, 100)
}

/* 验证邮箱 */
export function validateEmail(rule, value, callback) {
  const emailReg = /^(([a-zA-Z0-9_\-\.]+)@([a-zA-Z0-9_\-\.]+)\.([a-zA-Z]{2,5}){1,25})$/
  if (!value) {
    return callback(new Error('不能为空'))
  }
  setTimeout(() => {
    if (!emailReg.test(value)) {
      return callback(new Error('邮箱格式为：用户标识符+ @ + 域名'))
    }
    else {
      callback()
    }}, 100)
}

/* 验证所属单位 */
export function validateWorkUnit(rule, value, callback) {
  if (!value) {
    return callback(new Error('不能为空'))
  }
  else{
    callback()
  }
}

/* 验证所在地区 */
export function validateDistrict(rule, value, callback) {
  if (!value) {
    return callback(new Error('不能为空'))
  }
  else{
    callback()
  }
}







