<template>
  <div id="base_register">
    <el-form :model="registerForm" :rules="registerRules" class="register_container" label-position="left"
             label-width="0px" v-loading="loading" :ref="registerForm">
      <h3 class="register_title">Register</h3>
      <el-form-item prop="username" required >
        <el-input type="text" v-model="registerForm.username" id="usn"
                  auto-complete="off" placeholder="username"></el-input>
      </el-form-item>
      <el-form-item prop="password" required id="psw">
        <el-input type="password" v-model="registerForm.password"
                  auto-complete="off" placeholder="password"></el-input>
      </el-form-item>
      <el-form-item prop="email" required>
        <el-input type="text" v-model="registerForm.email"
                  auto-complete="off" placeholder="email"></el-input>
      </el-form-item>
      <el-form-item prop="workUnit" required>
        <el-input type="text" v-model="registerForm.workUnit"
                  auto-complete="off" placeholder="workUnit"></el-input>
      </el-form-item>
      <el-form-item prop="district" required="true">
        <el-input type="text" v-model="registerForm.district"
                  auto-complete="off" placeholder="district"></el-input>
      </el-form-item>
      <el-form-item style="width: 100%">
        <el-button type="primary" style="width: 40%;background: #afb4db;border: none" v-on:click="register(registerForm)">register</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>



<style scoped>
  #base_register{
    background: url("../assets/background/background.jpg") repeat;
    background-position: center;
    height: 100%;
    width: 100%;
    background-size: cover;
    position: fixed;
  }
  .register_container{
    border-radius: 15px;
    background-clip: padding-box;
    margin: 90px auto;
    width: 350px;
    padding: 35px 35px 15px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
  }
  .register_title{
    margin: 0 auto 40px auto;
    text-align: center;
    color: #505458;
  }
</style>

<script>

  import { validateUsername,validateEmail,validateWorkUnit,validateDistrict } from '../js/validate'
  export default {
    name: 'Register',
    data () {
      /*
      const dataValid = (rule, value, callback) => {
        if(!value || value === '') {
          return callback(new Error('不能为空'))
        }
        return callback()
      }
      */
      return {
        registerForm: {
          username: '',
          password: '',
          email: '',
          workUnit: '',
          district: ''
        },
        registerRules: {
          username: [{ required: true, trigger: 'blur', validator: validateUsername }],
          password: [{ required: true, trigger: 'blur', validator: validatePassword }],
          email: [{ required: true, trigger: 'blur', validator: validateEmail}],
          workUnit: [{required: true, message: '', trigger: 'blur', validator: validateWorkUnit}],
          district: [{required: true, message: '', trigger: 'blur', validator: validateDistrict}]
        },
        loading: false
      }
    },

    methods: {
      userTypeChange() {
      },
      register (formName) {
        this.$refs[formName].validate(valid => {
          if(valid){
            this.$axios.post('/register', {
                username: this.registerForm.username,
                password: this.registerForm.password,
                email: this.registerForm.email,
                workUnit: this.registerForm.workUnit,
                district: this.registerForm.district
                //authorities: [this.registerForm.usertype]
              }
            )
              .then(resp => {
                // 根据后端的返回数据修改
                if(resp.status === 200) { // && resp.data.hasOwnProperty("id")
                  // 跳转到login
                  alert('successful registration')
                  this.$router.replace('/login')
                } else{
                  alert('register error')
                }
              })
              .catch(error => {
                console.log(error)
                alert(error.message)
              })
          } else {
            alert('wrong submit')
          }
        })
      }
    }

  }



  function validatePassword(rule, value, callback) {
    const passwordReg = /^(?![\d]+$)(?![a-zA-Z]+$)(?![-_]+$)[\da-zA-Z-_]{6,32}$/;
    if (!value) {
      return callback(new Error('不能为空'))
    }
    if(document.getElementById("usn").value!=null){
      if (value.indexOf(document.getElementById("usn").value)!== -1){
        return callback(new Error('密码中不能包含用户名'))
      }
    }
    setTimeout(() => {
      if (!passwordReg.test(value)) {
        return callback(new Error('密码中字母、数字或者特殊字符（-_）至少包含两种，长度为6-32个字符'))
      }
      else {
        callback()
      }}, 100)
  }


</script>



